import React from "react";

const LevelContainer = ({ onClick }) => {
  return (
    <div className="contents">
      <QuestionCard
        title={ '' }
        contents={ `!. \n     .` }
        level={ LEVEL.INTERMEDIATE }
        onClick={ onClick }
      />

      <QuestionCard
        title={ '' }
        contents={ '  . \n     .' }
        level={ LEVEL.ADVANCED }
        onClick={ onClick }
      />
    </div>
  );
};

const QuestionCard = ({ title, contents, onClick, level }) => {
  return (
    <div className="card">
      <h3>{ title }</h3>
      <div className="main_text">
        <p>{ contents.split( '\n' ).map( (content,index) => {
          return ( <span key={index}>{ content }<br/></span> )
        } ) }
        </p>
      </div>
      <button className="btn" onClick={ onClick } data-level={ level }></button>
    </div>
  );
};

const LEVEL = {
  INTERMEDIATE: 'intermediate',
  ADVANCED: 'advanced',
};

export default LevelContainer;
